import Img from 'gatsby-image'
import React from 'react'
import styled from 'react-emotion'
import { borders, colors, transitions } from '../theme'

const imgStyle = {
  position: 'absolute',
  left: 0,
  top: 0,
  width: '100%',
  height: '100%',
}

const StyledTestimonial = styled.div`
  margin: 10px;
  border: 1px solid #eee;
  border-radius: ${borders.radius.normal};
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transition: ${transitions.all};
  min-width: 400px;
  max-width: 450px;

  .picture {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 200px;
    width: 100%;
    transition: ${transitions.all};
  }

  .content {
    font-size: 1.3rem;
    padding: 20px;
    font-weight: normal;
    flex-grow: 1;
    color: ${colors.text.normal};
  }

  .footer {
    display: flex;
    padding: 20px;
  }

  .author-picture {
    position: relative;
    overflow: hidden;
    background-repeat: no-repeat;
    background-position: center;
    width: 100px;
    height: 100px;
    flex-shrink: 0;
    margin-right: 15px;
    border-radius: ${borders.radius.small};
  }

  .author {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    font-size: 1.1rem;

    .name {
      font-weight: 600;
    }
    .title,
    .company {
      color: ${colors.text.light3};
    }

    .company-logo {
      position: relative;
      width: 40%;
      max-height: 30px;
      height: 100%;
      margin-top: 8px;
    }
  }

  @media (max-width: 768px) {
    min-width: 300px;

    .author .company-logo {
      width: 70%;
    }
  }
`

const Testimonial = ({ picture, author, children }) => (
  <StyledTestimonial>
    <div className="picture">
      <Img sizes={picture.sizes} alt={picture.title} style={imgStyle} />
    </div>
    <p className="content">{children}</p>
    <div className="footer">
      <div className="author-picture">
        <Img
          sizes={author.picture.sizes}
          alt={author.picture.title}
          style={imgStyle}
        />
      </div>
      <div className="author">
        <p className="name">{author.name}</p>
        <p className="title">{author.job}</p>
        <div className="company-logo">
          <img
            src={author.company.logo.file.url}
            alt={author.company.logo.title}
            style={imgStyle}
          />
        </div>
      </div>
    </div>
  </StyledTestimonial>
)

export default Testimonial
export { StyledTestimonial }



