import React from 'react'
import styled from 'react-emotion'
import Testimonial, { StyledTestimonial } from '../components/Testimonial'
import { colors, layout } from '../theme'

const StyledTestimonialList = styled.div`
  h4.section-title {
    padding: 0 ${layout.containerPadding.normal};
    margin-top: 30px;
    font-weight: normal;
    text-align: center;
    color: ${colors.text.light3};
    font-size: 1.5rem;
  }

  .testimonials {
    display: flex;
    flex-wrap: wrap;
    padding: 15px 10px;
    align-items: stretch;
    justify-content: center;
    margin-top: 50px;
    padding-bottom: 20px;

    ${StyledTestimonial} {
      flex-grow: 1;
      flex-basis: 0;
    }
  }
`

const TestimonialList = ({ testimonialList }) => (
  <StyledTestimonialList>
    <div className="testimonials">
      {testimonialList.testimonials.map(t => (
        <Testimonial picture={t.picture} author={t.author} key={t.id}>
          {t.content.content}
        </Testimonial>
      ))}
    </div>
  </StyledTestimonialList>
)

export default TestimonialList
export { StyledTestimonialList }



