import React from 'react'
import styled from 'react-emotion'
import Helmet from 'react-helmet'
import BanquetForAll from '../components/BanquetForAll'
import Footer from '../components/Footer'
import Navbar from '../components/Navbar'
import TestimonialList from '../components/TestimonialList'
import { colors, layout } from '../theme'

const StyledEvents = styled.section`
  h1 {
    text-align: center;
    font-size: 2.5rem;
    font-weight: 900;
    color: ${colors.text.normal};
    max-width: 722px;
    margin: 0 auto;
    padding: 0 ${layout.containerPadding.normal};
    padding-top: 150px;
  }
  h2 {
    color: ${colors.text.light3};
    font-size: 1.5rem;
    font-weight: normal;
    max-width: 655px;
    text-align: center;
    margin: 0 auto;
    margin-top: 30px;
    padding: 0 ${layout.containerPadding.normal};
  }
  h3 {
    padding: 0 ${layout.containerPadding.normal};
    margin-top: 50px;
    text-align: center;
    color: ${colors.text.normal};
    font-size: 2rem;
  }

  @media (max-width: 768px) {
    h1 {
      padding-top: 100px;
      font-size: 2rem;
    }
  }
`

const Events = props => (
  <StyledEvents>
    <Helmet>
      <title>
        Témoignages : Les histoires de +20.000 événements réussis •
        Monbanquet.fr
      </title>
      <meta
        name="description"
        content="Nous avons participé aux événements de plus de 3000 entreprises pour régaler plus de 200.000 convives. Voici leurs histoires de rencontre et de partage !"
      />
    </Helmet>
    <Navbar />
    <h1>{props.data.testimonialList.title}</h1>
    <h2>{props.data.testimonialList.subTitle}</h2>
    <TestimonialList testimonialList={props.data.testimonialList} />
    <BanquetForAll>
      Organisez des moments de partage de qualité pour votre équipe et vos
      clients : nous nous chargeons d’apporter de quoi manger.
    </BanquetForAll>
    <Footer />
  </StyledEvents>
)

export const query = graphql`
  query TestimonialsQuery {
    testimonialList: contentfulTestimonialList(
      id: { eq: "c1ojWbhrSbySqcekY2O2Mcy" }
    ) {
      title
      subTitle
      testimonials {
        id
        content {
          content
        }
        picture {
          title
          sizes(maxWidth: 300) {
            ...GatsbyContentfulSizes_withWebp
          }
        }
        author {
          name
          job
          picture {
            title
            sizes(maxWidth: 300) {
              ...GatsbyContentfulSizes_withWebp
            }
          }
          company {
            name
            logo {
              title
              file {
                url
              }
            }
          }
        }
      }
    }
  }
`

export default Events
export { StyledEvents }



